<template>
  <div class="button-wrap">
    <button
      v-if="endedState"
      type="button"
      aria-label="replay button"
      class="btn btn-player btn-replay"
      @click="emitReplay()"
    ></button>

    <button
      v-else
      type="button"
      aria-label="mute button"
      class="btn btn-player"
      :class="`btn-mute-${muteState ? 'on' : 'off'}`"
      @click="emitMuteToggle()"
    ></button>
  </div>
</template>

<script>
export default {
  name: 'VideoMuteBtn',

  props: {
    endedState: {
      type: Boolean,
      default: false
    },
    muteState: {
      type: Boolean,
      default: true
    }
  },

  methods: {
    emitMuteToggle () {
      this.$emit('muteClicked', !this.muteState)
    },
    emitReplay () {
      this.$emit('replayClicked')
    }
  }
}
</script>

